'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import Link from 'next/link';
import {
  MapPin,
  Clock,
  Bus,
  Calendar,
  Navigation,
  Users,
  CheckCircle,
  Star,
  Activity,
  ArrowRight,
  Bell,
  Settings,
  TrendingUp,
  Award,
  Zap,
  Globe,
  Shield,
  Heart,
  Smartphone,
  Wifi,
  Battery,
  Signal,
  Route,
  Timer,
  DollarSign,
  AlertCircle
} from 'lucide-react';

export default function StaffDashboard() {
  const [currentTime, setCurrentTime] = useState(new Date());
  const [liveBuses, setLiveBuses] = useState([]);
  const [todayStats, setTodayStats] = useState({
    totalTrips: 0,
    activeRoutes: 0,
    onTimePerformance: 0,
    availableSeats: 0
  });
  const [weather, setWeather] = useState({ temp: 28, condition: 'Sunny', humidity: 65 });

  useEffect(() => {
    const timer = setInterval(() => setCurrentTime(new Date()), 1000);
    return () => clearInterval(timer);
  }, []);

  const fleetStats = [
    { icon: Bus, value: "18", label: "Active Buses", color: "bg-primary", change: "+2", trend: "up" },
    { icon: DollarSign, value: "₦2.8M", label: "Today's Revenue", color: "bg-accent", change: "+12%", trend: "up" },
    { icon: Activity, value: "156", label: "Daily Trips", color: "bg-chart-5", change: "+8", trend: "up" },
    { icon: Wrench, value: "4", label: "In Maintenance", color: "bg-chart-4", change: "-1", trend: "down" }
  ];

  const activeBuses = [
    {
      id: 'BUS-001',
      route: "Lagos Express",
      driver: { name: "Emeka Johnson", rating: 4.9 },
      capacity: 45,
      occupied: 38,
      status: "on-time",
      location: "Mile 12, Lagos",
      fuelLevel: 85,
      revenue: 197000,
      alerts: []
    },
    {
      id: 'BUS-002', 
      route: "Campus Shuttle",
      driver: { name: "Fatima Adebayo", rating: 4.8 },
      capacity: 30,
      occupied: 22,
      status: "arriving",
      location: "UI Main Gate",
      fuelLevel: 62,
      revenue: 78900,
      alerts: ["Low fuel warning"]
    },
    {
      id: 'BUS-003',
      route: "Abuja Metro",
      driver: { name: "Ibrahim Musa", rating: 4.6 },
      capacity: 50,
      occupied: 12,
      status: "delayed",
      location: "Central Business District",
      fuelLevel: 45,
      revenue: 234500,
      alerts: ["Running 10 mins late", "Low occupancy"]
    }
  ];

  const maintenanceQueue = [
    {
      id: 'BUS-012',
      issue: 'Engine Service Due',
      priority: 'high',
      estimatedTime: '4 hours',
      technician: 'Abdul Rasheed',
      status: 'in-progress',
      progress: 65
    },
    {
      id: 'BUS-007',
      issue: 'Brake Pad Replacement',
      priority: 'medium',
      estimatedTime: '2 hours',
      technician: 'Kemi Okafor',
      status: 'scheduled',
      progress: 0
    },
    {
      id: 'BUS-015',
      issue: 'AC System Check',
      priority: 'low',
      estimatedTime: '1 hour',
      technician: 'Ahmed Sule',
      status: 'pending',
      progress: 0
    }
  ];

  const recentAlerts = [
    { message: 'BUS-001 due for service in 2 days', type: 'maintenance', priority: 'medium', time: '1h ago', icon: Wrench },
    { message: 'BUS-002 fuel level below 30%', type: 'fuel', priority: 'high', time: '2h ago', icon: Fuel },
    { message: 'Lagos Express route experiencing delays', type: 'delay', priority: 'low', time: '3h ago', icon: Clock },
    { message: 'New driver Ibrahim Musa completed training', type: 'driver', priority: 'info', time: '5h ago', icon: UserCheck }
  ];

  return (
    <div className="h-full bg-gradient-to-br from-background via-muted/30 to-secondary/5">
      <div className="max-w-7xl mx-auto px-3 py-2 space-y-3">
        
        {/* Header Section */}
        <div className="mb-2">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-3">
            <div>
              <h1 className="text-3xl font-bold text-foreground mb-1 animate-gradient-text">
                Welcome back, Staff Member! 👨‍💼
              </h1>
              <p className="text-lg text-muted-foreground mb-2">Fleet Command Center</p>
              <p className="text-base text-muted-foreground flex items-center gap-2">
                <Shield className="h-4 w-4 text-primary" />
                {currentTime.toLocaleDateString('en-NG', { 
                  weekday: 'long', 
                  month: 'long', 
                  day: 'numeric' 
                })} • {currentTime.toLocaleTimeString('en-NG', { 
                  hour: '2-digit', 
                  minute: '2-digit' 
                })}
              </p>
            </div>
            
            <div className="flex flex-wrap gap-3">
              <Button className="gradient-bg-primary btn-interactive glow-primary hover-lift">
                <Settings className="h-4 w-4 mr-2" />
                Fleet Control
              </Button>
              <Button variant="outline" className="border-2 border-border hover:border-primary/50 feature-card">
                <BarChart3 className="h-4 w-4 mr-2" />
                Analytics
              </Button>
            </div>
          </div>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 mb-3">
          {fleetStats.map((stat, index) => (
            <Card key={index} className="modern-card feature-card group animate-scale-in stagger-${index + 1}">
              <CardContent className="p-4">
                <div className="flex items-center justify-between mb-3">
                  <div className={`p-3 rounded-full ${stat.color} group-hover:animate-pulse`}>
                    <stat.icon className="h-6 w-6 text-white" />
                  </div>
                  <div className="text-right">
                    <p className="text-2xl font-bold text-foreground">{stat.value}</p>
                    <p className="text-sm text-muted-foreground">{stat.label}</p>
                  </div>
                </div>
                <div className="flex items-center justify-between">
                  <span className={`text-sm font-medium flex items-center gap-1 animate-breathe ${
                    stat.trend === 'up' ? 'text-accent' : 'text-chart-1'
                  }`}>
                    <TrendingUp className={`h-4 w-4 ${stat.trend === 'down' ? 'rotate-180' : ''}`} />
                    {stat.change}
                  </span>
                  <span className="text-xs text-muted-foreground">vs yesterday</span>
                </div>
                <div className={`absolute bottom-0 left-0 w-full h-1 ${stat.color} group-hover:h-2 transition-all duration-300`} />
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Main Dashboard Tabs */}
        <Tabs defaultValue="fleet" className="space-y-4">
          <TabsList className="grid w-full grid-cols-4 lg:w-auto lg:grid-cols-4 h-12 modern-card">
            <TabsTrigger value="fleet" className="flex items-center gap-2 data-[state=active]:gradient-bg-primary data-[state=active]:text-white">
              <Bus className="h-4 w-4" />
              Fleet Status
            </TabsTrigger>
            <TabsTrigger value="analytics" className="flex items-center gap-2 data-[state=active]:gradient-bg-primary data-[state=active]:text-white">
              <BarChart3 className="h-4 w-4" />
              Analytics
            </TabsTrigger>
            <TabsTrigger value="maintenance" className="flex items-center gap-2 data-[state=active]:gradient-bg-primary data-[state=active]:text-white">
              <Wrench className="h-4 w-4" />
              Maintenance
            </TabsTrigger>
            <TabsTrigger value="alerts" className="flex items-center gap-2 data-[state=active]:gradient-bg-primary data-[state=active]:text-white">
              <Bell className="h-4 w-4" />
              Alerts
            </TabsTrigger>
          </TabsList>

          <TabsContent value="fleet" className="space-y-3 animate-fade-in">
            <div className="grid grid-cols-1 xl:grid-cols-4 gap-3">

              {/* Active Fleet */}
              <Card className="xl:col-span-3 modern-card animate-slide-in-left">
                <CardHeader className="gradient-bg-primary text-white">
                  <CardTitle className="flex items-center gap-2 text-xl">
                    <Navigation className="h-6 w-6 animate-float" />
                    Active Fleet ({fleetStats[0].value} buses)
                  </CardTitle>
                </CardHeader>
                <CardContent className="p-4 space-y-3">
                  {activeBuses.map((bus, index) => (
                    <div key={bus.id} className="p-4 modern-card hover-lift group animate-slide-in-up border-l-4 border-primary" style={{ animationDelay: `${index * 0.1}s` }}>
                      <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-4">
                          <div className="relative group-hover:animate-bounce">
                            <Bus className="h-8 w-8 text-primary" />
                            <div className={`absolute -top-1 -right-1 w-3 h-3 rounded-full animate-pulse ${
                              bus.status === 'on-time' ? 'bg-accent' :
                              bus.status === 'arriving' ? 'bg-chart-4' : 'bg-chart-1'
                            }`} />
                          </div>
                          <div>
                            <div className="flex items-center gap-3 mb-1">
                              <h4 className="text-lg font-bold text-foreground group-hover:text-primary transition-colors">{bus.id}</h4>
                              <Badge variant="outline" className="text-xs font-medium bg-primary/10 text-primary border-primary/20">
                                {bus.route}
                              </Badge>
                            </div>
                            <p className="text-sm text-muted-foreground flex items-center gap-2">
                              <UserCheck className="h-3 w-3" />
                              {bus.driver.name} • ⭐ {bus.driver.rating}
                            </p>
                          </div>
                        </div>
                        <Badge variant={bus.status === 'on-time' ? 'default' : bus.status === 'arriving' ? 'secondary' : 'destructive'} className="text-sm animate-breathe">
                          {bus.status === 'on-time' ? 'On Time' : bus.status === 'arriving' ? 'Arriving' : 'Delayed'}
                        </Badge>
                      </div>
                      
                      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
                        <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <MapPin className="h-4 w-4" />
                          <span className="truncate">{bus.location}</span>
                        </div>
                        <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Users className="h-4 w-4" />
                          <span>{bus.occupied}/{bus.capacity} passengers</span>
                        </div>
                        <div className="flex items-center gap-2 text-sm">
                          <Fuel className="h-4 w-4 text-muted-foreground" />
                          <span className={bus.fuelLevel < 30 ? 'text-chart-1 font-semibold animate-pulse' : 'text-muted-foreground'}>
                            {bus.fuelLevel}% fuel
                          </span>
                        </div>
                        <div className="flex items-center gap-2 text-sm">
                          <DollarSign className="h-4 w-4 text-muted-foreground" />
                          <span className="font-semibold text-accent">₦{bus.revenue.toLocaleString()}</span>
                        </div>
                      </div>
                      
                      <div className="flex items-center justify-between">
                        <Progress value={(bus.occupied / bus.capacity) * 100} className="w-32 h-3 group-hover:h-4 transition-all" />
                        <div className="flex gap-2">
                          <Button variant="outline" size="sm" className="btn-interactive hover-lift">
                            <MessageSquare className="h-4 w-4 mr-1" />
                            Message
                          </Button>
                          <Button variant="outline" size="sm" className="btn-interactive hover-lift">
                            <Navigation className="h-4 w-4 mr-1" />
                            Track
                          </Button>
                        </div>
                      </div>

                      {bus.alerts.length > 0 && (
                        <div className="mt-3 space-y-1 animate-slide-in-up">
                          {bus.alerts.map((alert, index) => (
                            <div key={index} className="flex items-center gap-2 text-xs text-chart-4 bg-chart-4/10 px-3 py-1 rounded-full animate-pulse">
                              <AlertTriangle className="h-3 w-3" />
                              {alert}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>
                  ))}
                </CardContent>
              </Card>

              {/* Quick Controls */}
              <Card className="modern-card animate-slide-in-right">
                <CardHeader className="bg-gradient-to-r from-chart-5 to-chart-1 text-white">
                  <CardTitle className="flex items-center gap-2 text-xl">
                    <Zap className="h-6 w-6 animate-float-delay-1" />
                    Quick Actions
                  </CardTitle>
                </CardHeader>
                <CardContent className="p-4 space-y-3">
                  <Button className="w-full justify-start gap-3 h-12 gradient-bg-primary btn-interactive hover-lift text-white">
                    <div className="p-2 bg-white/20 rounded group-hover:animate-bounce">
                      <Bus className="h-4 w-4" />
                    </div>
                    <div className="text-left">
                      <div className="font-medium">Dispatch Bus</div>
                      <div className="text-xs opacity-90">Send bus to route</div>
                    </div>
                  </Button>

                  <Button className="w-full justify-start gap-3 h-12 bg-accent hover:bg-accent/90 btn-interactive hover-lift text-white">
                    <div className="p-2 bg-white/20 rounded group-hover:animate-bounce">
                      <MessageSquare className="h-4 w-4" />
                    </div>
                    <div className="text-left">
                      <div className="font-medium">Driver Alert</div>
                      <div className="text-xs opacity-90">Send notifications</div>
                    </div>
                  </Button>

                  <Button className="w-full justify-start gap-3 h-12 bg-chart-4 hover:bg-chart-4/90 btn-interactive hover-lift text-white">
                    <div className="p-2 bg-white/20 rounded group-hover:animate-bounce">
                      <Wrench className="h-4 w-4" />
                    </div>
                    <div className="text-left">
                      <div className="font-medium">Maintenance</div>
                      <div className="text-xs opacity-90">Schedule service</div>
                    </div>
                  </Button>
                  
                  <div className="pt-4 border-t space-y-3">
                    <div className="flex justify-between text-sm">
                      <span className="text-muted-foreground">System Status</span>
                      <Badge className="bg-accent/20 text-accent text-xs animate-breathe">All Online</Badge>
                    </div>
                    <div className="flex justify-between text-sm">
                      <span className="text-muted-foreground">Last Update</span>
                      <span className="font-medium text-xs text-foreground">Just now</span>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="maintenance" className="space-y-3 animate-fade-in">
            <Card className="modern-card animate-slide-in-up">
              <CardHeader className="bg-gradient-to-r from-chart-4 to-chart-1 text-white">
                <CardTitle className="flex items-center gap-2 text-xl">
                  <Wrench className="h-6 w-6 animate-float" />
                  Maintenance Queue ({maintenanceQueue.length} buses)
                </CardTitle>
              </CardHeader>
              <CardContent className="p-4">
                <div className="space-y-3">
                  {maintenanceQueue.map((item, index) => (
                    <div key={index} className="p-4 modern-card hover-lift group animate-slide-in-up border-l-4 border-chart-4" style={{ animationDelay: `${index * 0.1}s` }}>
                      <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-3">
                          <Badge variant="outline" className="font-mono bg-primary/10 text-primary border-primary/20">{item.id}</Badge>
                          <div>
                            <h4 className="font-bold text-foreground group-hover:text-primary transition-colors">{item.issue}</h4>
                            <p className="text-sm text-muted-foreground">Technician: {item.technician}</p>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          <Badge variant={
                            item.priority === 'high' ? 'destructive' :
                            item.priority === 'medium' ? 'default' : 'secondary'
                          } className="text-xs animate-breathe">
                            {item.priority} priority
                          </Badge>
                          <Badge variant="outline" className="text-xs bg-muted group-hover:bg-primary/10 transition-colors">
                            {item.status === 'in-progress' ? (
                              <><Activity className="h-3 w-3 mr-1 animate-pulse" /> In Progress</>
                            ) : item.status === 'scheduled' ? (
                              <><Timer className="h-3 w-3 mr-1" /> Scheduled</>
                            ) : (
                              <><Clock className="h-3 w-3 mr-1" /> Pending</>
                            )}
                          </Badge>
                        </div>
                      </div>
                      
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-4 text-sm text-muted-foreground">
                          <span>Est. time: {item.estimatedTime}</span>
                          {item.progress > 0 && (
                            <span className="text-accent font-medium">Progress: {item.progress}%</span>
                          )}
                        </div>
                        {item.progress > 0 && (
                          <Progress value={item.progress} className="w-32 h-3 group-hover:h-4 transition-all" />
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="alerts" className="space-y-3 animate-fade-in">
            <Card className="modern-card animate-slide-in-up">
              <CardHeader className="bg-gradient-to-r from-chart-1 to-chart-4 text-white">
                <CardTitle className="flex items-center gap-2 text-xl">
                  <Bell className="h-6 w-6 animate-float-delay-2" />
                  System Alerts ({recentAlerts.length})
                </CardTitle>
              </CardHeader>
              <CardContent className="p-4">
                <div className="space-y-3">
                  {recentAlerts.map((alert, index) => (
                    <div key={index} className="flex items-start gap-4 p-4 modern-card hover-lift group animate-slide-in-up" style={{ animationDelay: `${index * 0.1}s` }}>
                      <div className={`p-2 rounded-full group-hover:animate-pulse ${
                        alert.priority === 'high' ? 'bg-chart-1/20' :
                        alert.priority === 'medium' ? 'bg-chart-4/20' :
                        alert.priority === 'low' ? 'bg-primary/20' : 'bg-accent/20'
                      }`}>
                        <alert.icon className={`h-5 w-5 ${
                          alert.priority === 'high' ? 'text-chart-1' :
                          alert.priority === 'medium' ? 'text-chart-4' :
                          alert.priority === 'low' ? 'text-primary' : 'text-accent'
                        }`} />
                      </div>
                      <div className="flex-1">
                        <div className="flex items-center justify-between mb-2">
                          <p className="font-semibold text-foreground group-hover:text-primary transition-colors">{alert.message}</p>
                          <Badge variant={
                            alert.priority === 'high' ? 'destructive' :
                            alert.priority === 'medium' ? 'default' :
                            alert.priority === 'low' ? 'secondary' : 'outline'
                          } className="text-xs animate-breathe">
                            {alert.priority}
                          </Badge>
                        </div>
                        <p className="text-sm text-muted-foreground">{alert.time}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="analytics" className="space-y-3 animate-fade-in">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-3">

              <Card className="modern-card animate-slide-in-left">
                <CardHeader className="bg-gradient-to-r from-chart-5 to-chart-1 text-white">
                  <CardTitle className="flex items-center gap-2 text-xl">
                    <DollarSign className="h-6 w-6 animate-float" />
                    Revenue Analytics
                  </CardTitle>
                </CardHeader>
                <CardContent className="p-4">
                  <div className="space-y-3">
                    <div className="flex justify-between items-center p-3 feature-card bg-accent/10 hover-lift group">
                      <span className="font-medium text-muted-foreground">Today</span>
                      <span className="text-xl font-bold text-accent group-hover:animate-pulse">₦2.8M</span>
                    </div>
                    <div className="flex justify-between items-center p-3 feature-card hover-lift group">
                      <span className="font-medium text-muted-foreground">Yesterday</span>
                      <span className="text-lg font-semibold text-foreground">₦2.5M</span>
                    </div>
                    <div className="flex justify-between items-center p-3 feature-card hover-lift group">
                      <span className="font-medium text-muted-foreground">This Week</span>
                      <span className="text-lg font-semibold text-foreground">₦18.6M</span>
                    </div>
                    <div className="flex justify-between items-center p-3 feature-card hover-lift group">
                      <span className="font-medium text-muted-foreground">This Month</span>
                      <span className="text-lg font-semibold text-foreground">₦78.2M</span>
                    </div>
                  </div>
                </CardContent>
              </Card>

              <Card className="modern-card animate-slide-in-right">
                <CardHeader className="gradient-bg-primary text-white">
                  <CardTitle className="flex items-center gap-2 text-xl">
                    <TrendingUp className="h-6 w-6 animate-float-delay-1" />
                    Performance Metrics
                  </CardTitle>
                </CardHeader>
                <CardContent className="p-4">
                  <div className="space-y-3">
                    <div className="flex justify-between items-center p-3 feature-card bg-primary/10 hover-lift group">
                      <span className="font-medium text-muted-foreground">Avg Occupancy</span>
                      <span className="text-xl font-bold text-primary group-hover:animate-pulse">78%</span>
                    </div>
                    <div className="flex justify-between items-center p-3 feature-card hover-lift group">
                      <span className="font-medium text-muted-foreground">On-Time Performance</span>
                      <span className="text-lg font-semibold text-foreground">94.5%</span>
                    </div>
                    <div className="flex justify-between items-center p-3 feature-card hover-lift group">
                      <span className="font-medium text-muted-foreground">Fuel Efficiency</span>
                      <span className="text-lg font-semibold text-foreground">12.8 km/l</span>
                    </div>
                    <div className="flex justify-between items-center p-3 feature-card hover-lift group">
                      <span className="font-medium text-muted-foreground">Customer Rating</span>
                      <span className="text-lg font-semibold text-foreground">⭐ 4.7/5</span>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>

        {/* Action Bar */}
        <Card className="modern-card gradient-bg-secondary text-white animate-glow-pulse overflow-hidden">
          <CardContent className="p-4 relative">
            <div className="absolute inset-0 bg-gradient-to-r from-primary/20 to-accent/20 animate-morphing"></div>
            <div className="relative z-10">
              <div className="flex flex-col lg:flex-row items-center justify-between gap-4">
                <div className="text-center lg:text-left animate-slide-in-left">
                  <h3 className="text-2xl font-bold mb-2 animate-gradient-text">Fleet Performance Overview</h3>
                  <p className="text-primary-foreground/90 text-lg">All systems operational • 18 buses active • Revenue up 12%</p>
                </div>
                <div className="flex flex-wrap gap-3 animate-slide-in-right">
                  <Button size="lg" variant="secondary" className="bg-white text-primary hover:bg-muted btn-interactive feature-card">
                    <BarChart3 className="h-5 w-5 mr-2" />
                    Generate Report
                  </Button>
                  <Button size="lg" variant="outline" className="glass-card border-white/20 text-white hover:bg-white/20 btn-interactive">
                    <Settings className="h-5 w-5 mr-2" />
                    System Settings
                  </Button>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}